<template>
  <div>
    <v-row>
      <v-col cols="6">
        <v-text-field
          label="本のタイトルを検索"
          v-model="keyword"
        ></v-text-field>
      </v-col>
    </v-row>
    <v-row>
      <v-col cols="3">
        <v-btn color="primary" @click="search(keyword)"> 検索する </v-btn>
      </v-col>
      <v-col col="3">
        <!-- v-btn内でto=pathとするとrouter-Linkのようにジャンプ可能 -->
        <v-btn color="secondary" to="/">一覧に戻る</v-btn>
      </v-col>
    </v-row>
    <v-row>
      <v-col
        cols="12"
        md="6"
        v-for="(book, index) in searchResults"
        :key="book.index"
      >
        <v-row>
          <v-card>
            <v-row>
              <v-col cols="4">
                <v-img :src="book.image"></v-img>
              </v-col>
              <v-col cols="8">
                <v-card-title>{{ book.title }}</v-card-title>
                {{ book.description }}
                <v-spacer></v-spacer>
                <v-card-actions>
                  <v-btn fab dark color="indigo" @click="addBookList(index)">
                    <v-icon dark> mdi-plus </v-icon>
                  </v-btn>
                </v-card-actions>
              </v-col>
            </v-row>
          </v-card>
        </v-row>
      </v-col>
    </v-row>
  </div>
</template>

<script>
export default {
  name: "BookSearch",
  data() {
    return {
      keyword: "",
      searchResults: [],
    };
  },
  methods: {
    addBookList(index) {
      // emitで設定するカスタムデータ属性はkebabu-caseで記載
      this.$emit("add-book-list", this.searchResults[index]);
    },

    // 非同期処理を宣言
    async search(keyword) {
      this.searchResults = [];
      const baseUrl = "https://www.googleapis.com/books/v1/volumes?";
      const params = {
        q: `inititle:${keyword}`,
        maxResults: 40,
      };
      // query-String(URL上に表記されるparam)を作成
      // URLSearchParamsオブジェクトはURLにqueryを設定する際に使用する
      const queryParams = new URLSearchParams(params);
      //->https://www.googleapis.com/books/vl/volumes?q=inititle%3A%24%7Bkeyword%7D&maxResults=40

      // fetchでリクエストを送り、promiseオブジェクトとしてレスポンスが返る
      // その後thenメソッドでresponseをjson形式から変換、最後にそれを変数に入れる
      const response = await fetch(baseUrl + queryParams).then((response) =>
        response.json()
      );
      console.log(response.items);
      // 情報を配列にpush
      for (let book of response.items) {
        let title = book.volumeInfo.title;
        let img = book.volumeInfo.imageLinks;
        let description = book.volumeInfo.description;
        this.searchResults.push({
          title: title ? title : "",
          image: img ? img.thumbnail : "",
          description: description ? description.slice(0, 40) : "",
        });
      }
    },
  },
};
</script>

<style></style>
